import React from 'react';
import { MergePropsType } from '../../common';
import { SlotWrap, SlotTitle } from './styled';

export type ISlotProps = MergePropsType<
  {
    /**
     * 上方的图标
     */
    icon?: React.ReactNode;
    /**
     * 下方的标题文字，建议 4 个字符以内，超过会显示 ...
     */
    title?: string;
  },
  React.ButtonHTMLAttributes<HTMLDivElement>
>;

export default function Slot({ title, icon, ...rest }: ISlotProps) {
  return (
    <SlotWrap {...rest}>
      {!!icon && icon}
      {!!title && <SlotTitle>{title}</SlotTitle>}
    </SlotWrap>
  );
}
